import './App.css';
import react from 'react';
import { BrowserRouter as Router, Switch, Route, NavLink, } from "react-router-dom";

import Home from "./components/Home";
import Users from "./components/Users";
import About from "./components/About";
import Error404 from "./components/Error404";
function App() {
  return (
    <Router>
      <div>
        <nav>
          <ul>
            <li>
              <NavLink exact activeClassName="active" to="/">Home</NavLink>
            </li>
            <li>
              <NavLink activeClassName="active" to="/about">About</NavLink>
            </li>
            <li>
              <NavLink activeClassName="active" to="/users">Users</NavLink>
            </li>
          </ul>
        </nav>
        {
          /*
          No Match (404):
          Hata ekranlarn nasl karlayabiliriz?
          Kullanc gelip bizim var olmayan bir endpointimiz var diyelim.
          rnek: localhost:3000/asdfasf
          Buraya girmeye alt anda normal grnty almak yerine
          hata ekran gsterimi yapabiliriz.
          Bunu yapmak iinde linklerimizde yine Route kullanyoruz.
          Path olarak * vericez. Hata sayfamz iin componentimizi oluturuyoruz.
          Burada yldz koymamzn sebebi her trl ele diyoruz.
          Yani yukardaki Routelerin hi biriyle elemediyse her trl bununla ele
          nki hi biriyle elemediyse yle bir ekran yoktur. Dolaysyla bununla elemesi yeterli diyoruz.
          */
        }
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/about" component={About} />
          <Route path="/users" component={Users} />
          <Route path="*" component={Error404} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
